<template>
  <va-card :title="$t('tables.labelSupplierlist')" class="w-100">
    <va-button
      icon="fa fa-plus"
      flat
      slot="actions"
      class="mr-0"
      @click="showCreateModal"
    />
    <div class="row mr-0">
      <div class="flex xs12 sm4">
        <label class="input-label"> {{$t('forms.search')}} </label>
        <va-input v-model="searchText" @input="searchDelay" removable :placeholder="$t('forms.search')" @keyup.enter="search"  />
      </div>
      <!-- <va-button
        icon="fa fa-search"
        flat
        slot="actions"
        color="success"
        class="mr-0"
        @click="search"
      />
      <va-button
        icon="fa fa-trash"
        flat
        slot="actions"
        color="danger"
        class="mr-0"
        @click="deletesearch"
      /> -->

    </div>
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      :no-pagination = "searchBool"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{(props.rowIndex + 1) + ((pageNumber  -1) *10) }}
      </template>
      <template slot="is_vip" slot-scope="props">
        {{props.rowData.is_vip  ? '&#10004;': '&#10008;' }}
      </template>
      <template slot="accounts" slot-scope="props">
        {{props.rowData.accounts === null ? '-----': props.rowData.accounts.name }}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>

        <va-button flat small color="danger" @click="showDeleteModal(props.rowData)" class="ma-0">
          {{ $t('tables.delete') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="deleteModal" no-outside-dismiss
      :title=" $t('generic.deleteItemTitle') "
      :okText=" $t('modal.delete') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.deleteItemMsg') "
      @ok="okDeletefun()"
      @cancel="cancleDeletefun()">
    </va-modal>

    <va-modal v-model="createModal" no-outside-dismiss
      size="large"
      :title="$t(modelTitle)"
      hide-default-actions >
      <form @submit.prevent="editModal? okEditfun()  :okCreatefun()">
        <div class="row flex-center">
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.name')+' *'}} </label>
            <va-input v-model="currentobject.name"
              :error="riquierdvalidation(currentobject.name)"
              :error-messages="$t('errorMessage.required')"
              :placeholder="$t('placeholder.name')" required/>
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.email')}} </label>
            <va-input v-model="currentobject.email"
              :error="validateEmail(currentobject.email)"
              :error-messages="$t('errorMessage.email')"
              :placeholder="$t('placeholder.email')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.phone')+' *'}} </label>

            <va-input v-model="currentobject.phone"
              :error="validatePhone(currentobject.phone)"
              :error-messages="$t('errorMessage.phone')"
              :placeholder="$t('placeholder.phone')" required/>
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.ssn')}} </label>
            <va-input v-model="currentobject.ssn"
              :placeholder="$t('placeholder.ssn')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.location_Long')}} </label>
            <va-input v-model="currentobject.location_Long" type="number"
              :placeholder="$t('placeholder.location_Long')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.location_Lat')}} </label>
            <va-input v-model="currentobject.locaation_Lat" type="number"
              :placeholder="$t('placeholder.location_Lat')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.address')}} </label>
            <va-input v-model="currentobject.address"
              :placeholder="$t('placeholder.address')" />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.city') +' *'}} </label>
            <va-select
              :label="$t('placeholder.city')"
              v-model="fKCity.value"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fKCity.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-checkbox
              :label="$t('placeholder.is_vip')"
              v-model="currentobject.is_vip"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.notes')}} </label>
            <va-input v-model="currentobject.notes"
              :placeholder="$t('placeholder.notes')" />
          </div>

        </div>
        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="editModal? cancleEditfun() : cancleCreatefun()">
            {{ $t('modal.cancel') }}
          </va-button>
          <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t(modelOktext) }}</va-button>
        </div>
      </form>

    </va-modal>
  </va-card>

</template>

<script>
import customerslistService from '../../../../services/customers/customerslistService'
import CityService from '../../../../services/BasicData/CityService'
import { debounce } from 'lodash'

import helper from '../../../../helper/helper'
export default {
  data () {
    return {
      modelTitle: '',
      modelOktext: '',
      stopBtn: false,
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      deleteModal: false,
      editModal: false,
      createModal: false,
      loading: true,
      opjectBeforeEdit: {},
      currentobject: {},
      fKCity: {
        value: '',
        options: [],
      },
      searchText: '',
      searchBool: false,
      pageNumber: 1,
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',

      }, {
        name: 'name',
        title: this.$t('tables.headings.name'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|name',
      },
      {
        name: 'phone',
        title: this.$t('tables.headings.phone'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|phone',
      },
      {
        name: '__slot:is_vip',
        title: this.$t('tables.headings.is_vip'),
      },
      {
        name: 'address',
        title: this.$t('tables.headings.address'),
      },
      {
        name: 'maxDepit',
        title: this.$t('tables.headings.maxDepit'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|maxDepit',
      }, {
        name: 'notes',
        title: this.$t('tables.headings.notes'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|notes',
      }, {
        name: '__slot:accounts',
        title: this.$t('tables.headings.name_Accounts'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|accounts',
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  created () {
    this.readItems()
    this.getcites()
  },
  methods: {
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    numircvalidation (data) {
      return helper.numircvalidation(data)
    },
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },
    validateEmail (d = '') {
      if (d === null || d === undefined || d === '') {
        return false
      }
      return d.length >= 1 ? helper.validateEmail(d) : false
    },
    validatePhone (d = '') {
      if (d === null || d === undefined || d === '') {
        return false
      }
      return d.length >= 1 ? helper.validatePhone(d) : false
    },
    /*  api get */
    sortCallBack (obj) {
      this.readItems(1, (obj[0].toString().charAt(0).toUpperCase() + obj[0].toString().slice(1)))
    },
    readItems (page = 1, orderBy = null) {
      this.pageNumber = page
      this.loading = true
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        OrderBy: orderBy,
        Customer_type: 2,
      }
      this.pageNumber = page

      customerslistService.getAllPagnation(params)
        .then(response => {
          this.loading = false
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getcites () {
      CityService.getAll()
        .then(response => {
          this.fKCity.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    searchDelay: debounce(function (searchBarCodeTerm) {
      this.search()
    }, 800),

    search () {
      this.loading = true
      if (this.searchText === '') {
        return this.readItems()
      }

      const params = {
        Text: this.searchText,
      }
      this.dataTable = []
      customerslistService.search(params)
        .then(response => {
          this.dataTable = response.data
          this.totalPages = Math.ceil(this.dataTable.length / 10)
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },

    deletesearch () {
      this.searchText = ''
      this.searchBool = false
      this.readItems()
    },
    /*  end api get */
    /* cud api */
    create () {
      if (this.fKCity.value !== '') {
        this.currentobject.fK_City = this.fKCity.value.id
      }
      this.currentobject.fK_Customer_Type = 2
      customerslistService.create(this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.loading = false
          this.currentobject = {}
          this.fKCity.value = ''
          this.createModal = false
          this.stopBtn = false

          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    edit (user) {
      if (this.fKCity.value !== '') {
        this.currentobject.fK_City = this.fKCity.value.id
      }
      this.currentobject.fK_Customer_Type = 2

      customerslistService.update(this.currentobject.id, this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.currentobject = {}
          this.fKCity.value = ''
          this.loading = false
          this.editModal = false
          this.createModal = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    remove (id) {
      customerslistService.delete(id).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataTable.findIndex(u => u.id === id)
          this.dataTable.splice(idx, 1)
          this.loading = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    /* cud api */

    /* Delete model */
    showDeleteModal (cuobject) {
      this.deleteModal = true
      this.currentobject = cuobject
    },
    okDeletefun () {
      this.remove(this.currentobject.id)
    },
    cancleDeletefun () {
      this.loading = false
      this.stopBtn = false
    },
    /* end Delete model */

    /* edit model */
    showEditModal (cuobject) {
      this.editModal = true
      this.modelTitle = 'generic.editItemTitle'
      this.modelOktext = 'modal.edit'
      this.createModal = true
      this.fKCity.value = cuobject.city
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      this.currentobject.Customer_Type = 2
      this.loading = true
    },
    okEditfun () {
      this.stopBtn = true
      this.edit(this.currentobject)
    },
    cancleEditfun () {
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.createModal = false
      this.loading = false
      this.currentobject = {}
      this.fKCity.value = ''
      this.editModal = false
      this.stopBtn = false
    },
    /* end edit model */

    /* create model */
    showCreateModal () {
      this.modelTitle = 'generic.creatItemTitle'
      this.modelOktext = 'modal.create'
      this.createModal = true
      this.loading = true
    },
    okCreatefun () {
      this.stopBtn = true
      this.create()
    },
    cancleCreatefun () {
      this.createModal = false
      this.loading = false
      this.currentobject = {}
      this.fKCity.value = ''
      this.stopBtn = false
    },
    /* end create model */
  },
}

</script>

<style lang="scss" >
  i.fa.fa-exclamation {
    color: red !important;
  }

  .va-modal__inner {
    max-width: 100%;
  }

</style>
